<script>
import GuiField from './GlovoField'
import GuiTextInput from './GlovoTextInput'
import FormValidators from '@/services/formvalidators/FormValidators'

export default {
  name: 'GuiTextField',
  components: {
    GuiField,
    GuiTextInput
  },
  props: {
    labelKey: { type: String, default: null },
    prop: { type: String, default: null },
    type: { type: String, default: 'text' },
    isEditMode: { type: Boolean, default: null },
    required: { type: Boolean, default: false },
    placeholder: { type: String, default: null },
    placeholderKey: { type: String, default: null },
    label: { type: String, default: null },
    value: { type: String, default: null }
  },
  computed: {
    inputType() {
      if (this.type === 'email' || this.type === 'tel') {
        return 'text'
      }
      return this.type
    },
    inputRules() {
      const rules = this.rules || []
      if (this.type === 'email') {
        rules.push({
          type: 'email',
          message: this.$t('form.validation.invalid.email')
        })
      } else if (this.type === 'tel') {
        rules.push({
          validator: FormValidators.createPhoneValidator(
            this.$t('form.validation.invalid.phone')
          ),
          message: this.$t('form.validation.invalid.phone')
        })
      }
      return rules
    },
    editMode() {
      if (this.isEditMode == null) {
        return (
          (this.$parent && this.$parent.$attrs['data-is-edit-mode']) || false
        )
      }
      return this.isEditMode
    },
    placeholderLabel() {
      if (this.placeholder == null) return this.label
      return this.placeholder
    },
    placeholderLabelKey() {
      if (this.placeholderKey == null && !this.placeholder) return this.labelKey
      return this.placeholderKey
    },
    text: {
      get: function () {
        return this.value
      },
      set: function (newValue) {
        this.$emit('input', newValue)
      }
    }
  }
}
</script>

<template>
  <gui-field
    class="gui-text-field"
    :label-key="labelKey"
    :label="label"
    :required="required"
    :prop="prop"
    :rules="inputRules"
    :is-edit-mode="editMode"
    data-qa="label"
  >
    <span class="gui-text-field__text">{{
      type === 'tel' ? $phone(text) : text
    }}</span>
    <gui-text-input
      slot="edit"
      v-model="text"
      :type="inputType"
      :placeholder="placeholderLabel"
      :placeholder-key="placeholderLabelKey"
      data-qa="textbox"
    />
  </gui-field>
</template>

<style lang="scss" scoped>
@import '~styles';

.gui-text-field {
  &__text {
    font-family: $font-family-bold;
  }
}
</style>
