
import Component from 'vue-class-component'
import Vue from 'vue'
import HelpMenuBlog from './HelpMenuBlog.vue'
import HelpMenuMessage from './HelpMenuMessage.vue'
import HelpMenuContact from './HelpMenuContact.vue'
import HelpMenuRemitly from './HelpMenuRemitly.vue'
import HelpMenuItem from './HelpMenuItem.vue'
import { Getter } from 'vuex-class'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import {
  FeatureToggle,
  STRING_FEATURE_TOGGLE_EMPTY_VALUE
} from '@/modules/feature-toggles/store/state'
import { authentication } from '@/modules/authentication/store/namespace'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { StoreAdmin } from '@/modules/store/data/resources'
import GlovoStores from '@/modules/store/data/resources/glovo-stores'
import { GLOVO_STORES } from '@/modules/profile/components/constants/glovo-stores'

@Component({
  name: 'HelpMenuItems',
  components: {
    HelpMenuBlog,
    HelpMenuMessage,
    HelpMenuContact,
    HelpMenuItem,
    HelpMenuRemitly
  }
})
export default class HelpMenuItems extends Vue {
  glovoStores: GlovoStores = GLOVO_STORES

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  user!: StoreAdmin

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  get isBlogEnabled() {
    return this.toggles.FE_PARTNERS_PROFILE_BLOG_ENABLED
  }

  get shouldDisplayLinkToPartnerBlog() {
    return !this.isAndroidInterfaceEnabled && this.isBlogEnabled
  }

  get isBagsButtonEnabled() {
    return this.toggles.FE_PARTNERS_ASK_FOR_BAGS
  }

  get isAndroidInterfaceEnabled() {
    return window.Android
  }

  get glovoStoreIsEnabledForThisUser() {
    const glovoStoreKeys = Object.keys(this.glovoStores)
    return glovoStoreKeys.includes(this.userCountryCode)
  }

  get userCountryCode() {
    const { city = { countryCode: '' } } = this.user || {}
    return city.countryCode
  }

  get shouldDisplayBagsItem() {
    return (
      this.isAndroidInterfaceEnabled &&
      this.isBagsButtonEnabled &&
      this.glovoStoreIsEnabledForThisUser
    )
  }

  get shouldDisplayRemitly() {
    return (
      String(this.toggles.FE_PARTNERS_REMITLY_URL) !==
      STRING_FEATURE_TOGGLE_EMPTY_VALUE
    )
  }
}
