
import Component from 'vue-class-component'
import Vue from 'vue'
import { Watch } from 'vue-property-decorator'
import { Action } from 'vuex-class'
import ContactDetails from './ContactDetails.vue'
import StoreAddressProfile from './StoreAddressProfile.vue'
import GBreadcrumb from 'common/GBreadcrumb.vue'
import {
  GET_CURRENT_STORE_ADMIN_GETTER,
  GET_SELECTED_STORE_ADDRESS_IDS_GETTER
} from '@/modules/authentication'
import { authentication } from '@/modules/authentication/store/namespace'
import { ToBeMigrated } from 'types/any'
import {
  StoreAdmin,
  StoreAddressId,
  CityDTO
} from '@/modules/store/data/resources'
import { StoreAction } from '@/modules/store/types/store'
import { AddressData } from '@/modules/profile/types/AddressData'
import { UpdateSchedulePayload } from '@/modules/profile/types/UpdateSchedulePayload'
import { Contact, ContactDetailsState } from '@/modules/profile/types/Contact'

@Component({
  name: 'ProfileStores',
  components: {
    ContactDetails,
    GBreadcrumb,
    StoreAddressProfile
  }
})
export default class ProfileStores extends Vue {
  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER) user!: StoreAdmin
  @authentication.Getter(GET_SELECTED_STORE_ADDRESS_IDS_GETTER)
  selectedStoreAddresses!: StoreAddressId[]

  @Action('getStatus') getStatus!: ToBeMigrated
  @Action('getClosingDays') getClosingDays!: ToBeMigrated
  @Action('getInvoicingContactDetails')
  getInvoicingContactDetails!: StoreAction<StoreAddressId>
  @Action('getBusinessContactDetails') getBusinessContactDetails!: StoreAction<
    StoreAddressId
  >
  @Action('getDailyOperationContactDetails')
  getDailyOperationContactDetails!: StoreAction<StoreAddressId>

  @Action('updateSchedule') updateSchedule!: StoreAction<UpdateSchedulePayload>
  @Action('setInvoicingContactDetails')
  setInvoicingContactDetails!: StoreAction<{
    storeAddressId: StoreAddressId
    contact: Contact
  }>
  @Action('setBusinessContactDetails') setBusinessContactDetails!: StoreAction<{
    storeAddressId: StoreAddressId
    contact: Contact
  }>
  @Action('setDailyOperationContactDetails')
  setDailyOperationContactDetails!: StoreAction<{
    storeAddressId: StoreAddressId
    contact: Contact
  }>

  loading = false
  updatingSchedule = false
  contactDetails: ContactDetailsState = {
    invoicing: {},
    business: {},
    dailyOperation: {}
  }

  mounted() {
    this.updateInformationForSingleView()
  }

  @Watch('selectedStoreAddresses')
  onSelectedStoreAddressesChange() {
    this.updateInformationForSingleView()
  }

  get storeAddresses() {
    if (this.user && this.user.cities) {
      return this.getSelectedAddresses(this.user.cities)
    }
  }

  get hasOneStoreAddressSelected() {
    return this.selectedStoreAddresses.length === 1
  }

  get activeStoreAddress() {
    return this.hasOneStoreAddressSelected
      ? this.selectedStoreAddresses[0]
      : null
  }

  set activeStoreAddress(value) {
    // there's an error if no getter is implemented for v-model
  }

  async getInvoicingContactDetailsByStoreAddressId(
    storeAddressId: StoreAddressId
  ): Promise<void> {
    if (this.hasInvoicingContactLoaded(storeAddressId)) {
      return
    }
    const contact = await this.getInvoicingContactDetails(storeAddressId)
    this.$set(this.contactDetails.invoicing, storeAddressId, contact)
  }

  async getBusinessContactDetailsByStoreAddressId(
    storeAddressId: StoreAddressId
  ): Promise<void> {
    if (this.hasBusinessContactLoaded(storeAddressId)) {
      return
    }
    const contact = await this.getBusinessContactDetails(storeAddressId)
    this.$set(this.contactDetails.business, storeAddressId, contact)
  }

  async getDailyOperationContactDetailsByStoreAddressId(
    storeAddressId: StoreAddressId
  ): Promise<void> {
    if (this.hasDailyOperationContactLoaded(storeAddressId)) {
      return
    }
    const contact = await this.getDailyOperationContactDetails(storeAddressId)
    this.$set(this.contactDetails.dailyOperation, storeAddressId, contact)
  }

  async updateStoreAddressInformation(
    storeAddressId: StoreAddressId | null | undefined
  ): Promise<void> {
    if (storeAddressId) {
      this.loading = true
      await Promise.all([
        this.getStatus(storeAddressId),
        this.getClosingDays(storeAddressId),
        this.getInvoicingContactDetailsByStoreAddressId(storeAddressId),
        this.getBusinessContactDetailsByStoreAddressId(storeAddressId),
        this.getDailyOperationContactDetailsByStoreAddressId(storeAddressId)
      ])
    }
  }

  async updateAddressSchedule(data: UpdateSchedulePayload) {
    try {
      this.updatingSchedule = true
      await this.updateSchedule(data)
    } catch (e) {
      console.error(e)
    } finally {
      this.updatingSchedule = false
    }
  }

  async updateInvoicingContactDetails(
    storeAddressId: StoreAddressId
  ): Promise<void> {
    const contact = this.contactDetails.invoicing[storeAddressId]
    await this.setInvoicingContactDetails({ storeAddressId, contact })
    this.$set(this.contactDetails.invoicing, storeAddressId, contact)
  }

  async updateBusinessContactDetails(
    storeAddressId: StoreAddressId
  ): Promise<void> {
    const contact = this.contactDetails.business[storeAddressId]
    await this.setBusinessContactDetails({ storeAddressId, contact })
    this.$set(this.contactDetails.business, storeAddressId, contact)
  }

  async updateDailyOperationContactDetails(
    storeAddressId: StoreAddressId
  ): Promise<void> {
    const contact = this.contactDetails.dailyOperation[storeAddressId]
    await this.setDailyOperationContactDetails({ storeAddressId, contact })
    this.$set(this.contactDetails.dailyOperation, storeAddressId, contact)
  }

  hasInvoicingContactLoaded(storeAddressId: StoreAddressId): boolean {
    return !!(
      this.contactDetails && this.contactDetails.invoicing[storeAddressId]
    )
  }

  hasBusinessContactLoaded(storeAddressId: StoreAddressId): boolean {
    return !!(
      this.contactDetails && this.contactDetails.business[storeAddressId]
    )
  }

  hasDailyOperationContactLoaded(storeAddressId: StoreAddressId): boolean {
    return !!(
      this.contactDetails && this.contactDetails.dailyOperation[storeAddressId]
    )
  }

  updateInformationForSingleView() {
    if (this.selectedStoreAddresses.length === 1) {
      this.updateStoreAddressInformation(this.selectedStoreAddresses[0])
    }
  }

  getSelectedAddresses(cities: CityDTO[]) {
    const addresses: AddressData[] = []
    cities.forEach(({ code, stores }) => {
      stores.forEach(({ storeAddresses, name }) => {
        storeAddresses.forEach(
          ({ id, address, closedUntil, details, openingTimes }) => {
            if (this.selectedStoreAddresses.includes(id)) {
              const addressData = {
                id,
                store: name,
                city: code,
                address,
                closedUntil,
                details,
                openingTimes
              }
              addresses.push(addressData)
            }
          }
        )
      })
    })
    return addresses
  }
}
