
import { photosHost } from '@/api/localConfig'
import moment from 'moment-timezone'
import Icon from 'common/Icon.vue'
import Placeholder from 'common/Placeholder.vue'
import Schedule from './Schedule.vue'
import GlovoBtn from 'common/GlovoBtn.vue'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import {
  GET_STORE_ADDRESSES_GETTER,
  GET_STORES_GETTER
} from '@/modules/store/data/getters'
import Component from 'vue-class-component'
import { Action, Getter } from 'vuex-class'
import Vue from 'vue'
import { authentication } from '@/modules/authentication/store/namespace'
import { Prop } from 'vue-property-decorator'
import { ToBeMigrated } from 'types/any'
import {
  StoreAddressId,
  StoreAdmin,
  StoresAddressesMap,
  StoresMap
} from '@/modules/store/data/resources'
import { DEFAULT_SCHEDULE } from '@/modules/profile/components/constants/schedule'
import { DateYYYYMMDD } from 'types/dates'
import { DayDTO } from '@/api/user'
import OpeningTimes from '@/modules/store/data/resources/opening-times'

@Component({
  name: 'StoreAddressProfile',
  components: {
    Schedule,
    Placeholder,
    GlovoBtn,
    Icon
  }
})
export default class StoreAddressProfile extends Vue {
  @Prop({ required: true }) storeAddressId!: number
  @Prop({ default: false }) loading!: boolean

  @Getter('isDesktop') isDesktop!: boolean
  @Getter('closingDays') closingDays!: DayDTO[]
  @Getter('open') open!: boolean

  @Getter(GET_STORE_ADDRESSES_GETTER) storeAddresses!: StoresAddressesMap
  @Getter(GET_STORES_GETTER) stores!: StoresMap

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER) user!: StoreAdmin

  @Action('displayState') displayState!: ToBeMigrated
  @Action('changeModalDisplay') changeModalDisplay!: ToBeMigrated
  @Action('removeClosingDay') removeClosingDay!: ToBeMigrated
  @Action('setTicketCopies') setTicketCopies!: ToBeMigrated

  editSchedule = false
  calendarLoading = false
  tempOpeningTimes = { ...DEFAULT_SCHEDULE }

  mounted() {
    this.resetTempOpeningTimes()
  }

  get bgImage() {
    if (this.user) {
      const width = window.innerWidth
      return `url(${photosHost}c_fill,h_230,w_${width}/${this.user.store.imageId})`
    } else {
      return ''
    }
  }

  get storeAddress() {
    return this.storeAddresses[this.storeAddressId]
  }

  get store() {
    return this.stores[this.storeAddress.parentStoreId]
  }

  resetTempOpeningTimes() {
    const mapped = { ...DEFAULT_SCHEDULE }
    const openingTimes = this.storeAddress.openingTimes
    for (const key in openingTimes) {
      if (openingTimes.hasOwnProperty(key)) {
        const day = (key as any) as keyof OpeningTimes // small hack to make the types work (quite hard to, otherwise)
        const schedule = openingTimes[day]
        const firstDay = schedule![0] || { opening: null, closing: null }
        const secondDay = schedule![1] || { opening: null, closing: null }
        mapped[day] = [
          { opening: firstDay.opening, closing: firstDay.closing },
          { opening: secondDay.opening, closing: secondDay.closing }
        ]
      }
    }
    this.tempOpeningTimes = mapped
  }

  closeScheduleDialog() {
    this.resetTempOpeningTimes()
    this.editSchedule = false
  }

  saveSchedule(storeAddressId: StoreAddressId) {
    this.$emit('update-schedule', {
      storeAddressId,
      schedule: this.tempOpeningTimes
    })
    this.editSchedule = false
  }

  showYear(holiday: DayDTO) {
    if (moment(holiday.start).year() === moment(holiday.end).year()) {
      return moment(holiday.start).year()
    }
    return `${moment(holiday.start).year()} - ${moment(holiday.end).year()}`
  }

  showMonth(date: DateYYYYMMDD) {
    return moment(date).format('DD/MMM')
  }

  showHolidayDuration(holiday: DayDTO) {
    const start = this.showMonth(holiday.start)
    const end = this.showMonth(holiday.end)
    return `${start} - ${end}`
  }

  async removeHoliday(holiday: DayDTO) {
    this.calendarLoading = true
    await this.removeClosingDay({
      interval: holiday,
      storeAddressId: this.storeAddress.id
    })
    this.calendarLoading = false
  }
}
