
import Component from 'vue-class-component'
import Vue from 'vue'
import { Getter, Action } from 'vuex-class'
import { ToBeMigrated } from 'types/any'

@Component({
  name: 'ProfileTickets'
})
export default class ProfileTickets extends Vue {
  @Getter('ticketCopies') ticketCopies!: number

  @Action('setTicketCopies') setTicketCopies!: ToBeMigrated

  decrementCopies() {
    if (this.ticketCopies > 1) {
      this.setTicketCopies(this.ticketCopies - 1)
    }
  }

  incrementCopies() {
    if (this.ticketCopies < 4) {
      this.setTicketCopies(this.ticketCopies + 1)
    }
  }
}
