
import Icon from 'common/Icon.vue'
import { GET_SELECTED_STORE_ADDRESS_IDS_GETTER } from '@/modules/authentication'
import Component from 'vue-class-component'
import Vue from 'vue'
import { Action, Getter } from 'vuex-class'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAddressId } from '@/modules/store/data/resources'
import { Watch } from 'vue-property-decorator'
import { ToBeMigrated } from 'types/any'

@Component({
  name: 'ProfileCommunication',
  components: {
    Icon
  }
})
export default class ProfileCommunication extends Vue {
  @Getter('communication') communication!: ToBeMigrated

  @authentication.Getter(GET_SELECTED_STORE_ADDRESS_IDS_GETTER)
  selectedStoreAddresses!: StoreAddressId[]

  @Action('getUnviewedCommunication') getUnviewedCommunication!: ToBeMigrated

  @Action('markCommunicationAsViewed') markCommunicationAsViewed!: ToBeMigrated

  async mounted() {
    if (this.selectedStoreAddresses.length >= 1) {
      this.loadCommunication(this.selectedStoreAddresses[0])
    }
  }

  get isVisible(): boolean {
    return !!(this.communication && this.communication.title)
  }

  @Watch('selectedStoreAddresses')
  onSelectedStoreAddressesChange() {
    if (this.selectedStoreAddresses.length >= 1) {
      this.loadCommunication(this.selectedStoreAddresses[0])
    }
  }

  async loadCommunication(storeAddressId: StoreAddressId) {
    try {
      await this.getUnviewedCommunication(storeAddressId)
    } catch (e) {
      console.error(e)
    }
  }

  async closeBanner() {
    await this.markCommunicationAsViewed({
      storeAddressId: this.selectedStoreAddresses[0],
      communicationId: this.communication.id
    })
    await this.loadCommunication(this.selectedStoreAddresses[0])
  }
}
