
import Component from 'vue-class-component'
import Vue from 'vue'
import { Getter, Action } from 'vuex-class'
import ProfileCommunication from './ProfileCommunication.vue'
import ProfileHeader from './ProfileHeader.vue'
import ProfileTickets from './ProfileTickets.vue'
import ProfileStores from './ProfileStores.vue'
import ProfileFooter from './ProfileFooter.vue'
import { DISPLAY_PROFILE } from '@/modules/store/services/display-types'
import { ToBeMigrated } from 'types/any'

@Component({
  name: 'Profile',
  components: {
    ProfileCommunication,
    ProfileHeader,
    ProfileTickets,
    ProfileStores,
    ProfileFooter
  }
})
export default class Profile extends Vue {
  @Getter('isDesktop') isDesktop!: boolean

  @Action('displayState') displayState!: ToBeMigrated

  beforeMount() {
    this.displayState(DISPLAY_PROFILE)
  }
}
