<script>
export default {
  name: 'GuiTextInput',
  props: {
    value: {
      type: String,
      default: null
    },
    type: {
      type: String,
      default: 'text'
    },
    placeholder: {
      type: String,
      default: ''
    },
    placeholderKey: {
      type: String,
      default: null
    }
  },
  computed: {
    text: {
      get: function () {
        return this.value
      },
      set: function (newValue) {
        this.$emit('input', newValue)
      }
    }
  }
}
</script>

<template>
  <el-input
    v-model="text"
    class="gui-text-input"
    :type="type"
    :placeholder="placeholderKey ? $t(placeholderKey) : placeholder"
    data-qa="textbox"
  />
</template>

<style scoped lang="scss">
.gui-text-input {
  background: white;
  /deep/ .el-input__inner {
    background: white;
    border-radius: 4px;
    border: solid 1px #d9dada;
    padding-left: 20px;
    padding-right: 20px;
    min-height: 40px;
    &:focus {
      transition: 0.4s;
      border-color: #f6c144;
    }
    &::placeholder {
      color: #cccccc;
    }
  }
}
</style>
