
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'

@Component({ name: 'Gui-Field' })
export default class GuiField extends Vue {
  @Prop({ default: '' }) label!: string
  @Prop({ default: null }) labelKey!: string
  @Prop({ default: null }) rules!: any
  @Prop({ default: null }) prop!: string
  @Prop({ default: false }) required!: boolean
  @Prop({ default: false }) isEditMode!: boolean

  get labelText() {
    if (this.labelKey) {
      return this.$t(this.labelKey) || this.label
    }
    return this.label
  }

  get innerInputRules() {
    const finalRules = []
    if (this.rules) {
      finalRules.push(...this.rules)
    }
    if (this.required) {
      finalRules.push({
        required: true,
        message: this.$t('form.field.is.required', { field: this.labelText })
      })
    }
    return finalRules
  }
}
