const NATURAL_NO_PATTERN = /^(0|([1-9]\d*))$/

export default class FormValidators {
  public static PHONE_REGEX = new RegExp('^\\+?[1-9]\\d{1,14}$')
  public static createPhoneValidator(errorMessage: string | any) {
    return FormValidators.createRegexValidator(
      errorMessage,
      FormValidators.PHONE_REGEX
    )
  }

  public static createRegexValidator(errorMessage: string, regex: RegExp) {
    return function (rule: any, value: any, callback: Function) {
      if (value && !new RegExp(regex).test(value)) {
        callback(new Error(errorMessage))
      } else {
        callback()
      }
    }
  }

  public static createEmptyOrNaturalNumberValidator(errorMessage: string) {
    return (rule: unknown, value: unknown, callback: Function) => {
      if (isEmpty(value) || isNaturalNumber(value)) {
        callback()
      } else {
        callback(new Error(errorMessage))
      }
    }
  }
}

const isEmpty = (value: unknown): boolean => {
  return value == null || value === ''
}

const isNaturalNumber = (value: unknown): value is number => {
  return NATURAL_NO_PATTERN.test(String(value))
}
