
import { GET_SELECTED_STORE_ADDRESS_IDS_GETTER } from '@/modules/authentication'
import { GET_STORE_ADDRESSES_GETTER } from '@/modules/store/data/getters'
import {
  DEFAULT_EMPTY_FORM,
  getRules,
  SUPPORT_PROBLEMS
} from '@/modules/profile/components/constants/support'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import Vue from 'vue'
import { Action, Getter } from 'vuex-class'
import { StoreAddress, StoreAddressId } from '@/modules/store/data/resources'
import { authentication } from '@/modules/authentication/store/namespace'
import { ToBeMigrated } from 'types/any'
import { ElForm } from '@/modules/core/plugins/element-ui'
import { KustomerProblem } from '@/modules/profile/types/KustomerProblem'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'

@Component({
  name: 'Support'
})
export default class Support extends Vue {
  @Prop() visible!: boolean

  @Getter(GET_STORE_ADDRESSES_GETTER) storeAddresses!: Record<
    StoreAddressId,
    StoreAddress
  >

  @authentication.Getter(GET_SELECTED_STORE_ADDRESS_IDS_GETTER)
  selectedStoreAddressIds!: StoreAddressId[]

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @Action('requestSupport') requestSupport!: ToBeMigrated

  form = { ...DEFAULT_EMPTY_FORM }

  get shouldShowChangesInput() {
    return this.form.problem === KustomerProblem.CONTENT_MODIFICATIONS
  }

  selectedAddressName(storeAddressId: StoreAddressId) {
    return this.storeAddresses[storeAddressId].address
  }

  async submitForm() {
    try {
      await this.requestSupport({ form: this.form, id: this.form.selectedId })
      this.$notify({
        message: this.$t('support.request_sent'),
        type: 'success'
      })
      this.close()
      return true
    } catch (err) {
      this.$notify({
        message: this.$t('support.request_failed'),
        type: 'error'
      })
      console.error(`Support ticket could not be sent - ${err}`)
      return false
    }
  }

  submit() {
    ;(this.$refs['form'] as ElForm).validate((isValid: boolean) => {
      if (isValid) {
        return this.submitForm()
      } else {
        return false
      }
    })
  }

  cancel() {
    this.close()
  }

  close() {
    this.reset()
    this.$emit('close')
  }

  reset() {
    this.form = { ...DEFAULT_EMPTY_FORM }
  }

  get problems() {
    let kustomerProblems = SUPPORT_PROBLEMS
    if (!this.toggles.FE_PARTNERS_NEW_CONTACT_REASON_ENABLED) {
      kustomerProblems = SUPPORT_PROBLEMS.filter(
        ({ kustomerValue }) => kustomerValue !== KustomerProblem.SOLICIT_REMAKE
      )
    }
    return kustomerProblems.map(({ kustomerValue, translationKey }) => ({
      label: this.$t(`support.problem.${translationKey}`),
      value: kustomerValue
    }))
  }

  get rules() {
    return getRules(this.$t)
  }
}
