
import ScheduleShift from './ScheduleShift.vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import Vue from 'vue'
import { DEFAULT_SCHEDULE } from '@/modules/profile/components/constants/schedule'
import OpeningTimes, {
  DayIndex
} from '@/modules/store/data/resources/opening-times'
import { Getter } from 'vuex-class'
import { green, greyC1 } from '@/modules/core/styles/export.scss'

@Component({
  name: 'Schedule',
  components: {
    ScheduleShift
  }
})
export default class Schedule extends Vue {
  private switchColour = {
    enabled: green,
    disabled: greyC1
  }

  @Prop({ default: false }) editMode!: boolean
  @Prop({ default: () => DEFAULT_SCHEDULE }) value!: OpeningTimes
  @Prop({ default: 2 }) firstDayOfWeek!: number
  @Prop({ default: false }) isModalMode!: boolean

  @Getter('isDesktop') isDesktop!: boolean

  openDayIndex = -1
  isOpenForEditing = false

  get weekdays() {
    const days = []
    const dayshift = this.firstDayOfWeek - 1
    for (let i = 0; i < 7; i++) {
      const day = ((dayshift + i) % 7) + 1
      days.push(day)
    }
    return days
  }

  get schedule() {
    const days: OpeningTimes = { ...DEFAULT_SCHEDULE }
    for (const i in this.value) {
      // @ts-ignore
      days[i] = this.value[i]
    }
    return days
  }

  get isModalInDesktop() {
    return this.isModalMode && this.isDesktop
  }

  get openingTimes() {
    return this.value
  }

  set openingTimes(newValue) {
    this.$emit('input', newValue)
  }

  isOpenForThisDay(weekday: DayIndex) {
    if (this.schedule[weekday]) {
      return (
        this.hasValidOpeningAndClosingTime(weekday, 0) ||
        this.hasValidOpeningAndClosingTime(weekday, 1)
      )
    }
    return false
  }

  hasValidOpeningAndClosingTime(weekday: DayIndex, timeIndex: 0 | 1) {
    return Boolean(
      this.schedule[weekday]![timeIndex] &&
        this.schedule[weekday]![timeIndex].opening &&
        this.schedule[weekday]![timeIndex].closing
    )
  }

  select(index: number) {
    if (!this.editMode) {
      return
    }
    this.openDayIndex = this.openDayIndex === index ? -1 : index
  }

  getDayForIndex(index: number) {
    const keysWithLongText = [
      'common.day1',
      'common.day2',
      'common.day3',
      'common.day4',
      'common.day5',
      'common.day6',
      'common.day7'
    ]
    const keysWithShortText = [
      'common.day.mon',
      'common.day.tue',
      'common.day.wed',
      'common.day.thu',
      'common.day.fri',
      'common.day.sat',
      'common.day.sun'
    ]
    const keys = this.isModalInDesktop ? keysWithLongText : keysWithShortText
    // shifting the day index to sync backend and translation keys
    // for backend 1 is Sunday, in translations 1 is monday
    const shiftedIndex = index === 1 ? 7 : index - 1
    const key = keys[shiftedIndex - 1]
    return this.$t(key)
  }
}
