<template>
  <div class="confirm">
    <modal-confirm-wrapper
      :disable-accept="disableAccept"
      @cancel="$emit('cancel')"
      @accept="checkAccept()"
    >
      <div slot="body" class="confirm-body">
        <div>{{ content }}</div>
        <div v-if="disableAccept" class="confirm-disable-reason">
          {{ disableReason }}
        </div>
      </div>
    </modal-confirm-wrapper>
  </div>
</template>
<script>
import ModalConfirmWrapper from 'common/ModalConfirmWrapper'
export default {
  name: 'ConfirmModal',
  components: {
    ModalConfirmWrapper
  },
  props: {
    content: {
      type: String,
      required: true
    },
    disableAccept: {
      type: Boolean
    },
    disableReason: {
      type: String,
      default: ''
    }
  },
  methods: {
    checkAccept() {
      if (!this.disableAccept) {
        this.$emit('accept')
      }
    }
  }
}
</script>
<style lang="scss">
@import '~styles';

.confirm {
  &-disable {
    &-reason {
      font-size: 12px;
      color: $warning;
      margin-top: 20px;
    }
  }
}
</style>
