
import {
  IS_ADMIN_MODE_GETTER,
  SET_ADMIN_MODE,
  GET_ADMIN_ROUTES_GETTER,
  GET_CURRENT_STORE_ADMIN_GETTER
} from '@/modules/authentication'
import Component from 'vue-class-component'
import { Action, Getter } from 'vuex-class'
import Vue from 'vue'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAdmin } from '@/modules/store/data/resources'
import { Category } from 'models/Category'
import { ToBeMigrated } from 'types/any'
import { StoreAction } from '@/modules/store/types/store'
import { SetAdminModePayload } from '@/modules/authentication/store/action-types'
import GlovoBtn from 'common/GlovoBtn.vue'
import { RestrictedView } from '@/modules/store/data/resources/restricted-view'
import PermissionsModal from '@/components/main/popups/PermissionsPopup.vue'
import ConfirmModal from '@/components/main/popups/Confirm.vue'
import HelpModal from '@/components/main/popups/help/Help.vue'
import Support from './Support.vue'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { GLOVO_STORES } from './constants/glovo-stores'
import GlovoStores from '@/modules/store/data/resources/glovo-stores'

@Component({
  name: 'ProfileHeader',
  components: {
    GlovoBtn,
    PermissionsModal,
    ConfirmModal,
    HelpModal,
    Support
  }
})
export default class ProfileHeader extends Vue {
  supportFormVisible = false
  glovoStores: GlovoStores = GLOVO_STORES

  @Getter('permissionsModalDisplayed')
  permissionsModalDisplayed!: boolean

  @Getter('adminModalDisplayed')
  adminModalDisplayed!: boolean

  @Getter('helpModalDisplayed')
  helpModalDisplayed!: boolean

  @Getter('defaultCategory')
  defaultCategory!: Category

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  user!: StoreAdmin

  @authentication.Getter(GET_ADMIN_ROUTES_GETTER)
  adminRoutes!: RestrictedView[]

  @authentication.Getter(IS_ADMIN_MODE_GETTER)
  isAdmin!: boolean

  @Action('changeModalDisplay') changeModalDisplay!: ToBeMigrated

  @authentication.Action(SET_ADMIN_MODE) setAdminMode!: StoreAction<
    SetAdminModePayload
  >

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  get adminMode() {
    return this.isAdmin
  }

  set adminMode(value) {
    if (value) this.changePermissionsModalDisplay(value)
    else this.changeAdminModalDisplay(true)
  }

  get isBagsButtonEnabled() {
    return this.toggles.FE_PARTNERS_ASK_FOR_BAGS
  }

  get isAndroidInterfaceEnabled() {
    return window.Android
  }

  get glovoStoreIsEnabledForThisUser() {
    const glovoStoreKeys = Object.keys(this.glovoStores)
    return glovoStoreKeys.includes(this.userCountryCode)
  }

  get shouldShowGlovoStoreButton() {
    return (
      !this.isAndroidInterfaceEnabled &&
      this.toggles.FE_PARTNERS_ASK_FOR_BAGS &&
      this.glovoStoreIsEnabledForThisUser
    )
  }

  get userCountryCode() {
    const { city = { countryCode: '' } } = this.user || {}
    return city.countryCode
  }

  openGlovoStore() {
    const url = this.glovoStores[this.userCountryCode]
    window.open(url)
  }

  changeAdminModalDisplay(display: boolean) {
    this.changeModalDisplay({ key: 'ADMIN', display })
  }

  changePermissionsModalDisplay(display: boolean) {
    this.changeModalDisplay({ key: 'PERMISSIONS', display })
  }

  changeHelpModalDisplay(display: boolean) {
    this.changeModalDisplay({ key: 'HELP', display })
  }

  exitAdmin() {
    this.setAdminMode(false)
    this.changeAdminModalDisplay(false)
    if (this.adminRoutes.includes(this.$route.name as RestrictedView)) {
      this.$router.push({ name: this.defaultCategory.route })
    }
  }

  toggleSupport(toggle: boolean) {
    this.supportFormVisible = toggle
  }

  openSupport() {
    this.changeHelpModalDisplay(false)
    this.toggleSupport(true)
  }
}
