
import { langs as _langs } from '@/modules/i18n/services/locales'
import store from 'models/Storage'
import moment from 'moment-timezone'
import { LOG_OUT } from '@/modules/authentication'
import Component from 'vue-class-component'
import Vue from 'vue'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAction } from '@/modules/store/types/store'
import GlovoBtn from 'common/GlovoBtn.vue'

@Component({
  name: 'ProfileFooter',
  components: { GlovoBtn }
})
export default class ProfileFooter extends Vue {
  currentLang: string | null = null
  langs = _langs

  @authentication.Action(LOG_OUT) logout!: StoreAction<void>

  mounted() {
    this.currentLang = this.$i18n.locale
  }

  changeLang(lang: string) {
    this.$i18n.locale = lang
    moment.locale(lang)
    store.set(store.items.LANG, lang)
  }

  logoutButtonDidClack() {
    this.logout()
  }

  capitaliseLangOption(code: string) {
    return code[0].toUpperCase() + code.slice(1, 2)
  }
}
