
import Vue from 'vue'
import Component from 'vue-class-component'
import GuiForm from 'common/ui/GlovoForm.vue'
import GuiTextField from 'common/ui/GlovoTextField.vue'
import { Prop, Watch } from 'vue-property-decorator'
import { ContactDetail } from '@/modules/store/data/resources/contactDetail'

@Component({
  name: 'ContactDetails',
  components: { GuiTextField, GuiForm }
})
export default class ContactDetails extends Vue {
  @Prop({ default: null }) titleKey!: string
  @Prop({
    default: () => ({
      name: null,
      surname: null,
      phone: null,
      whatsApp: null,
      email: null,
      secondEmail: null
    })
  })
  value!: ContactDetail
  @Prop()
  update!: () => Promise<void>
  editMode = false
  originalValue!: ContactDetail
  isDirty = false

  get actions() {
    if (this.editMode) {
      if (this.isDirty) {
        return [
          {
            key: 'save-form',
            labelKey: 'common.action.save',
            isValidationRequired: true
          }
        ]
      }
      return [{ key: 'cancel-form', labelKey: 'common.action.cancel' }]
    }

    return [{ key: 'edit-form', labelKey: 'common.action.edit' }]
  }

  get contact() {
    return this.value
  }

  set contact(newValue) {
    this.$emit('input', newValue)
  }

  private setOriginalValue(value: ContactDetail) {
    this.isDirty = false
    this.originalValue = {
      name: value.name,
      surname: value.surname,
      phone: value.phone,
      whatsApp: value.whatsApp,
      email: value.email,
      secondEmail: value.secondEmail
    }
  }

  @Watch('value')
  onValueChanged(value: ContactDetail) {
    this.setOriginalValue(value)
  }

  @Watch('contact', { deep: true })
  onContactChanged(value: ContactDetail) {
    this.isDirty = this.hasContactChanged(this.originalValue, value)
  }

  hasContactChanged(first: ContactDetail, second: ContactDetail) {
    if (first == null || second == null) {
      return false
    }
    return (
      this.isDifferent(first.name, second.name) ||
      this.isDifferent(first.surname, second.surname) ||
      this.isDifferent(first.email, second.email) ||
      this.isDifferent(first.secondEmail, second.secondEmail) ||
      this.isDifferent(first.phone, second.phone) ||
      this.isDifferent(first.whatsApp, second.whatsApp)
    )
  }

  isDifferent(first: string | null, second: string | null) {
    if (first == null) {
      first = ''
    }
    if (second == null) {
      second = ''
    }
    return first != second
  }

  async save() {
    try {
      await this.update()
      this.setOriginalValue(this.contact)
      this.editMode = false
    } catch (error) {
      console.log(error)
      this.$message.error({
        message: this.$t('contact.details.form.save.failed')
      })
    }
  }
}
