<script>
import GlovoBtn from '../GlovoBtn'

export default {
  name: 'GuiForm',
  components: { GlovoBtn },
  props: {
    titleKey: { type: String, default: null },
    title: { type: String, default: null },
    actions: { type: Array, default: () => [] },
    isEditMode: { type: Boolean, default: false },
    model: { type: Object, default: null }
  },
  computed: {
    isHeaderVisible() {
      return (
        this.title || this.titleKey || (this.actions && this.actions.length)
      )
    }
  },
  methods: {
    doAction(action) {
      if (action.isValidationRequired) {
        this.$refs.internalForm.validate(isValid => {
          if (isValid) {
            this.$emit(action.key)
          } else {
            this.$emit('error')
          }
        })
      } else {
        this.$emit(action.key)
      }
    }
  }
}
</script>

<template>
  <div class="g-form">
    <header v-if="isHeaderVisible" class="g-form__header" data-qa="header">
      <slot name="header">
        <h1 class="g-form__header__title">
          {{ titleKey ? $t(titleKey) : title }}
        </h1>
      </slot>
      <slot v-if="actions" name="header-actions">
        <div data-qa="actions">
          <glovo-btn
            v-for="action in actions"
            :key="action.key"
            type="transparent-green"
            :text="action.labelKey ? $t(action.labelKey) : action.label"
            data-qa="action"
            @clack="() => doAction(action)"
          />
        </div>
      </slot>
    </header>
    <el-form
      ref="internalForm"
      :model="model"
      :data-is-edit-mode="isEditMode"
      class="gui-inner-form"
    >
      <slot />
    </el-form>
  </div>
</template>

