
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import Vue from 'vue'
import { DEFAULT_PICKER_OPTIONS } from './constants/schedule'
import moment from 'moment-timezone'
import { NullableTimeSlot } from '@/modules/store/data/resources'

@Component({
  name: 'ScheduleShift'
})
export default class ScheduleShift extends Vue {
  @Prop({ default: false }) readOnly!: boolean
  @Prop({
    default: () => ({
      opening: null,
      closing: null
    })
  })
  value!: NullableTimeSlot

  pickerOptions = DEFAULT_PICKER_OPTIONS
  defaultTime = moment().utcOffset(0).startOf('day').toDate()

  get shift() {
    return this.value
  }

  set shift(newValue) {
    this.$emit('input', newValue)
  }

  get opening() {
    return this.createDateTime(this.shift.opening)
  }

  set opening(newValue: Date | null) {
    this.shift = {
      opening: !newValue ? null : this.formatTime(newValue),
      closing: this.shift.closing
    }
  }

  get defaultOpeningTime() {
    return this.opening ? this.opening : this.defaultTime
  }

  get closing() {
    return this.createDateTime(this.shift.closing)
  }

  set closing(newValue: Date | null) {
    this.shift = {
      opening: this.shift.opening,
      closing: !newValue ? null : this.formatTime(newValue)
    }
  }

  get defaultClosingTime() {
    return this.closing ? this.closing : this.defaultTime
  }

  createDateTime(time: string | null): Date | null {
    if (!time || time.toString().indexOf(':') === -1) {
      return null
    }
    const pieces = time.toString().split(':')
    if (pieces.length < 2) {
      return null
    }
    const date = new Date()
    date.setHours(parseInt(pieces[0]))
    date.setMinutes(parseInt(pieces[1]))
    return date
  }

  formatTime(date: Date | null): string | null {
    if (!date) return null
    const hours = date.getHours()
    const minutes = date.getMinutes()
    return `${hours < 10 ? '0' : ''}${hours}:${
      minutes < 10 ? '0' : ''
    }${minutes}`
  }
}
