
import Component from 'vue-class-component'
import Vue from 'vue'
import HelpMenuItem from './HelpMenuItem.vue'
import { authentication } from '@/modules/authentication/store/namespace'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { StoreAdmin } from '@/modules/store/data/resources'

@Component({
  name: 'HelpMenuContact',
  components: {
    HelpMenuItem
  }
})
export default class HelpMenuContact extends Vue {
  currentLang: string | null = null

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  user!: StoreAdmin

  mounted() {
    this.currentLang = this.$i18n.locale
  }

  get contactPhone() {
    if (this.user && this.user.csPhoneNumber) {
      if (this.currentLang === 'ar') {
        return `${this.user.csPhoneNumber.replace(/[+, /s]/g, '')}+`
      }
      return this.user.csPhoneNumber.replace(/\s/g, '')
    }
    return ''
  }
}
